input{border: none; outline: none;}
.newContainer{
    width: 100%;
}
.myPrdBox{
    border-bottom: 1px solid rgb(244, 244, 250);
    height: 4rem;
}
.myPrdMenu{
    width: 1024px;
    margin: auto;
    display: flex;
    align-items: center;
    height: 100%;
}
.myPrdMenu p {
    padding:0.5rem;
    margin-left:1rem ;
    font-size: 13px;
}
.myPrdMenu p:first-child{
    padding-left: 0;
    margin-left: 0;
}
.myPrdMenu p:not(:last-child):after {
    content: "";
    width: 1px;
    height:14px;
    border-right: 1px solid rgb(210,210,210);
    margin-left: 2rem;
}
.selected {
    color: rgb(255, 80, 88);
}


.newPrdContent{
    width: 1024px;
    margin: auto;
}
.newPrdContent .title {
    height: 100px;
    display: flex;
    align-items: center;
    font-size: 26px;
    border-bottom: 2px solid rgb(30, 29, 41);
}
.newPrdContent .title span {
    font-size: 1rem;
    color: rgb(255, 80, 88);
    margin-left: 32px;
}

.newContainer .contentBox{
    width: 1024px;
    margin: auto;
}
.newContainer .contentBox section {
    padding: 2rem 0px;
    display: grid;
    grid-template-columns: 10.5rem 1fr ;
    border-bottom: 1px solid rgb(220, 219, 228);
}

.newContainer .contentBox section:last-child{
    border-bottom: 0px;
}

.newContainer .contentBox section .sectionTitle{
    font-size: 18px;
    color: #212121;
}
.fileBox label{
    width: 200px;
    height: 200px;
    background-image: url('../img/img_none.png');
    cursor: pointer;
    border: 1px solid rgb(220, 219, 228);
    display: block;
    overflow: hidden;
}
.fileBox input{
    display: none;
}
.fileBox p {
    margin-top: 1.5rem;
    color:rgb(74, 164, 255);
    line-height: 1.5;
    font-size: 14px;

}
.fileBox p strong{
    font-weight: 500;
}


.newContainer .contentBox section.titleSec div {
    display: flex;
    align-items: center;
    border: 1px solid rgb(195, 194, 204);
    padding: 0 16px;
}
.newContainer .contentBox section.titleSec div input{
    font-size: 16px;
    height: 3rem;
    width: 100%;
}
.newContainer .contentBox section.adrressBox div div{
    display: flex;
}
.newContainer .contentBox section.adrressBox label {    
    width: 6.5rem;
    height: 3rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid rgb(195, 194, 204);
    margin-right: 16px;
    border-radius: 2px;
}
.newContainer .contentBox section.adrressBox label:hover{
    opacity: .5;
}
.newContainer .contentBox section.adrressBox input {
    display: none;
}
.selectedInfo{
    margin-top: 16px;
    width: 100%;
    background: rgb(244, 244, 250);
    height: 3rem;
    border: 1px solid rgb(195, 194, 204);
    padding: 0 1rem;
    box-sizing: border-box;
    display: flex;
    align-items: center;
}


.radioBox div {padding-left: 23px;}
.radioBox [type="radio"]:checked,
.radioBox [type="radio"]:not(:checked) {
  position: absolute;
  left: -9999px;
}
.radioBox [type="radio"]:checked + label,
.radioBox [type="radio"]:not(:checked) + label
{
  position: relative;
  padding-right: 55px;
  cursor: pointer;
  line-height: 20px;
  display: inline-block;
  color: #000;
}
.radioBox [type="radio"]:checked + label:before,
.radioBox [type="radio"]:not(:checked) + label:before {
  content: '';
  position: absolute;
  left: -23px;
  top: 2px;
  width: 15px;
  height: 15px;
  border:2px solid #ddd;
  border-radius: 100%;
  background: #fff;
}
.radioBox [type="radio"]:checked + label:before {
  border:2px solid rgb(247, 47, 51);
  border-radius: 100%;
}
.radioBox [type="radio"]:checked + label:after,
.radioBox [type="radio"]:not(:checked) + label:after {
  content: '';
  width:9px;
  height:9px;
  background: rgb(247, 47, 51);
  position: absolute;
  top:7px;
  left:-18px;
  border-radius: 100%;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}
.radioBox [type="radio"]:not(:checked) + label:after {
  opacity: 0;
}
.radioBox [type="radio"]:checked + label:after {
  opacity: 1;
}


section.priceSec div,
section.countSec div
{ 
    display: flex;
    align-items: center;

}
section.priceSec div div,
section.countSec div div
{
    margin-right: 1rem;   
}
section.priceSec div div input,
section.countSec div div input
 {
    font-size: 16px;   
    display: flex;
    align-items: center;
    height: 3rem;
    border: 1px solid rgb(195, 194, 204);
    padding:0 16px;     
}

section.contentSec div textarea {
    resize: none;
    outline: none;
    width: 100%;
    padding: 1rem;
    font-size: 16px;
    box-sizing: border-box;
    border: 1px solid rgb(195, 194, 204);
}

section.contentSec div textarea::placeholder,
section.priceSec input::placeholder
{
    font-family: 'Noto Sans KR', sans-serif ;
    color:rgb(195, 194, 204);
}

.newPrdFooter {
    position: fixed;
    z-index: 1;
    bottom: 0;
    width: 100%;
    height: 5.5rem;
    background: rgb(250, 250, 253);
    border-top: 1px solid rgb(220, 219, 228);
}
.newPrdFooter div {
    width: 1024px;
    margin: auto;
    display: flex;
    justify-content: end;

}
.newPrdFooter div button {
    height: 3.5rem;
    width: 10rem;
    color: rgb(255, 255, 255);
    font-size: 20px;
    font-weight: 700;
    border-radius: 2px;
    background: rgb(255, 80, 88);
    position: relative;
    transform: translate(0, 25%);
    cursor: pointer;
}
